import { useState } from "react";
import Marquee from "react-fast-marquee";
import { CSSTransition } from "react-transition-group";

import finish1 from "../../images/finish1.png";
import finish2 from "../../images/finish2.png";
import finish3 from "../../images/finish3.png";
import finalBackground from "../../images/final_background.png";
import "./styles.scss";
import ContactForm from "../contactForm/ContactForm";

const QuizFinish = ({ score }) => {
  const [visibleForm, setVisibleForm] = useState(false);

  const handleVisibleForm = () => {
    setVisibleForm(true);
  };
  let result = "";
  let title = "";
  let subtitle = "";
  let description = "";

  const resultsPhrases = [
    "your result is  - 0%-30%",
    "your result is  - 40%-70%",
    "your result is  - 70%-100%",
  ];
  const titles = ["brand newbie / rookie", "rising star", "an ace"];

  const subtitles = [
    "oh man, don't underestimate the value of your brand",
    "wow, you Revving up the branding engine",
    "damn, you're a pro! ",
  ];

  const descriptions = [
    "Based on your answers, you clearly know the ins and outs of running a business—big kudos! However, you can still harness the power of branding, and we're here to help.",
    "Kudos for recognizing the true value of branding for your business. Don't stop! It's evident that you have many heights to conquer, and we would be thrilled to support you on this journey.",
    "We know the hard work you put into your precious brand. What you do is awesome. Keep going, and we would love to see you shine even brighter.",
  ];

  if (score <= 3) {
    result = resultsPhrases[0];
    title = titles[0];
    subtitle = subtitles[0];
    description = descriptions[0];
  } else if (score <= 7) {
    result = resultsPhrases[1];
    title = titles[1];
    subtitle = subtitles[1];
    description = descriptions[1];
  } else {
    result = resultsPhrases[2];
    title = titles[2];
    subtitle = subtitles[2];
    description = descriptions[2];
  }

  return (
    <div className="QuizFinish">
      <div className="QuizFinish__background">
        <img src={finalBackground} alt={finalBackground} />
      </div>
      <Marquee autoFill={true} className="desktop">
        {result}
      </Marquee>
      <div className="QuizFinish__wrapper">
        <div className="QuizFinish__content">
          <h1 className="QuizFinish__title">{title}</h1>
          <p className="QuizFinish__subtitle">{subtitle}</p>
          <p className="QuizFinish__text">{description}</p>
          <Marquee className="mobile" autoFill={true}>
            {result}
          </Marquee>
        </div>
        <div className="QuizFinish__image">
          {score <= 3 && <img src={finish1} alt="finishQuiz" />}
          {score > 3 && score <= 7 && <img src={finish2} alt="finishQuiz" />}
          {score > 7 && <img src={finish3} alt="finishQuiz" />}
        </div>
        {!visibleForm && (
          <div className="QuizFinish__btn-wrapper">
            <div className="QuizFinish__btn-tip">
              Receive a complete 20 step free guide to branding on your email
            </div>
            <button
              className="QuizFinish__btn btn-green"
              onClick={handleVisibleForm}
            >
              i want <br /> this!
            </button>
          </div>
        )}
      </div>
      <CSSTransition
        in={visibleForm}
        timeout={100}
        classNames="visible"
        unmountOnExit
      >
        <ContactForm
          visibleForm={visibleForm}
          setVisibleForm={setVisibleForm}
        />
      </CSSTransition>
    </div>
  );
};

export default QuizFinish;
