import { useState } from "react";
import { draggableImages, draggableImagesBorder } from "../../utils/images";
import { Draggable } from "drag-react";
import "./styles.scss";

const DraggableImages = ({ positionChanged }) => {
  const [draggedIndex, setDraggedIndex] = useState(null);

  const handleDragStart = (index) => {
    setDraggedIndex(index);
  };

  const handleDragEnd = () => {
    setDraggedIndex(null);
  };

  const calculatePosition = (index, total, radius) => {
    const centerX = 50; // Horizontal center of the page as a percentage (50%)
    const centerY = 50; // Vertical center of the page as a percentage (50%)

    const angle = (index / total) * 360; // Calculate the angle for each image

    // Calculate the x and y coordinates based on the angle and radius
    const x = centerX + radius * Math.cos((angle * Math.PI) / 180);
    const y = centerY + radius * Math.sin((angle * Math.PI) / 180);

    return {
      left: `${x}%`,
      top: positionChanged ? "100%" : `${y}%`,
      transform: `translate(-50%, -50%)`,
      transition: positionChanged ? "top 0.9s ease-in-out" : "", // Smooth transition animation
    };
  };

  const radius = 50; // Adjust the radius as needed

  return (
    <div className={`draggable-wrapper ${positionChanged ? " animate" : ""}`}>
      {draggableImages.map((img, index) => {
        const imgBorder = draggableImagesBorder[index];
        return (
          <Draggable
            key={index}
            className={`draggable-item${positionChanged ? " animate" : ""}${
              draggedIndex === index ? " dragging" : ""
            }`}
            onDragStart={() => handleDragStart(index)}
            onDragEnd={handleDragEnd}
            style={{
              position: "absolute",
              zIndex: 9,
              cursor: "move",
              ...calculatePosition(index, draggableImages.length, radius),
            }}
          >
            <img src={img} alt={img} className="img" />
            <img src={imgBorder} alt={imgBorder} className="img-border" />
          </Draggable>
        );
      })}
    </div>
  );
};

export default DraggableImages;
