import { useState } from "react";
import { Formik, Field, Form, ErrorMessage } from "formik";
import * as Yup from "yup";
import { ReactComponent as Close } from "../../images/close.svg";
import contactDecor from "../../images/contactDecor.png";

import "./styles.scss";

const initialValues = {
  name: "",
  email: "",
  privacyPolicy: false,
};

const validationSchema = Yup.object({
  name: Yup.string().required("Name is required"),
  email: Yup.string()
    .email("Invalid email address")
    .required("Email is required"),
  privacyPolicy: Yup.boolean().oneOf([true], "Privacy policy must be accepted"),
});

const onSubmit = (values) => {
  // Handle form submission here
  console.log(values);
};

const ContactForm = ({ visibleForm, setVisibleForm }) => {
  const handleCloseClick = () => {
    setVisibleForm(false);
  };
  return visibleForm ? (
    <div className="ContactForm">
      <div className="ContactForm__overlay"></div>
      <div className="ContactForm__form-wrapper">
        <div className="ContactForm__form-header">
          <div>attention!</div>
          <div className="ContactForm__form-close" onClick={handleCloseClick}>
            <Close />
          </div>
        </div>
        <div className="ContactForm__form-body">
          <h2 className="ContactForm__form-title">
            Transform your product's benefits into a captivating story
          </h2>
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={onSubmit}
          >
            <Form>
              <div className="input-wrapper">
                <Field type="text" id="name" name="name" autoComplete="off" />
                <label className="label" htmlFor="name">
                  Name:
                </label>
                <ErrorMessage name="name" component="div" />
              </div>

              <div className="input-wrapper">
                <Field
                  type="email"
                  id="email"
                  name="email"
                  autoComplete="off"
                />
                <label className="label" htmlFor="email">
                  Email:
                </label>
                <ErrorMessage name="email" component="div" />
              </div>

              <div className="checkbox-wrapper">
                <label>
                  <Field type="checkbox" name="privacyPolicy" />
                  <span>
                    I agree with
                    <a href="#">Terms of Use</a> and
                    <a href="#">Privacy Policy.</a>
                  </span>
                </label>
                <ErrorMessage name="privacyPolicy" component="div" />
              </div>

              <button
                type="submit"
                className="ContactForm__form-submit btn-purple"
              >
                send me
              </button>
            </Form>
          </Formik>
          <div className="ContactForm__img">
            <img src={contactDecor} alt="decoration" />
          </div>
        </div>
      </div>
    </div>
  ) : null;
};

export default ContactForm;
