import { useRef } from "react";
import Lottie from "lottie-react";
import box from "../../animations/box.json";
import sneakers from "../../animations/sneakers.json";
import juice from "../../animations/juice.json";
import abstract1 from "../../animations/abstract1.json";
import "./styles.scss";

const QuizAnswer = ({
  currentQuestion,
  selectedOption,
  showQuizAnswer,
  questions,
  onAnimationComplete,
}) => {
  const lottieRef = useRef();

  const animations = [box, sneakers, juice, abstract1];

  const handleClick = () => {
    lottieRef.current?.play();
  };

  const handleAnimationComplete = () => {
    onAnimationComplete(true);
  };

  return (
    <div
      className={`QuizAnswer ${showQuizAnswer ? "QuizAnswer--visible" : ""}`}
    >
      <div className="QuizAnswer__wrapper">
        <h2 className="QuizAnswer__title">{selectedOption}</h2>
        <div className=" QuizAnswer__content">
          <div className="QuizAnswer__animation">
            <Lottie
              lottieRef={lottieRef}
              animationData={animations[currentQuestion]}
              loop={false}
              autoplay={false}
              onComplete={handleAnimationComplete}
            />
          </div>
          <div className="QuizAnswer__tip">
            <div className="QuizAnswer__tip-wrapper">
              <h3>answer</h3>
              <p>{questions[currentQuestion].answerTip}</p>
              <button
                className="QuizAnswer__next btn-purple"
                onClick={handleClick}
              >
                Next question
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default QuizAnswer;
