import "./styles.scss";

const QuizCard = ({ optionClicked, questions, currentQuestion }) => {
  return (
    <div className="QuizCard">
      <div className="container">
        <div className="QuizCard__wrapper">
          <div className="QuizCard__text">
            <h2 className="QuizCard__title">
              {questions[currentQuestion].question}
            </h2>
            <p className="QuizCard__tip">Choose just one option</p>
          </div>
          <div className="QuizCard__options">
            {questions[currentQuestion].options.map((option, idx) => {
              return (
                <div
                  key={idx}
                  onClick={() => optionClicked(option.answer, option.isCorrect)}
                  className="QuizCard__option"
                >
                  {option.answer}
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default QuizCard;
