import { ReactComponent as Logo } from "../../logo.svg";
import "./styles.scss";
import avatar from "../../images/avatar.png";

const Header = ({
  currentQuestion = 0,
  questions = [],
  positionChanged,
  isLast,
}) => {
  // const isLastQuestion = currentQuestion + 1 > questions.length;

  const isFirst = currentQuestion === 0;

  return (
    <header className="header">
      <div className="container">
        <div className="header__wrapper">
          <div className="header__links">
            <a href="/" className="header__logo">
              <Logo />
            </a>
            <a href="/" className="header__avatar">
              <img src={avatar} alt="avatar" />
            </a>
          </div>
          {isFirst && (
            <div
              className={`header__stage ${positionChanged ? "animate" : ""}`}
            >
              Intro
            </div>
          )}

          {!isLast && (
            <div
              className={`header__stage-count ${positionChanged ? "show" : ""}`}
            >
              <div className="currentQuestion">{currentQuestion + 1}</div>
              <div className="countQuestion">
                of <span>{questions.length}</span>
              </div>
            </div>
          )}

          {isLast && <div className="header__stage">Finish</div>}
        </div>
      </div>
    </header>
  );
};

export default Header;
